
html {
  font-size: 62.5%;
}
body {
  margin: 0;
  padding: 0;
  overflow: hidden;
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  position: absolute;
  width: 100%;
  height: 100%;
}

.my-dialog-hover {
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 999;
  text-align: center;
  background: rgba(0, 0, 0, 0.77);
}
.my-dialog-hover .dialog-container {
    z-index: 999;
    top: 50%;
    position: relative;
    -webkit-transform: translateY(-40%);
            transform: translateY(-40%);
}
.my-dialog-hover .top-box {
    position: relative;
    width: 66%;
    left: 17%;
    margin: 0;
    top: 1px;
}
.my-dialog-hover .top-box:before {
      content: "";
      position: absolute;
      z-index: 999;
      top: -8.2rem;
      left: 0;
      width: 0;
      height: 0;
      border-width: 8.2em 3.9rem 0 0;
      border-style: solid;
      border-color: transparent #fff;
}
.my-dialog-hover .top-box:after {
      content: "";
      position: absolute;
      top: -8.1rem;
      right: 0;
      left: 3.8rem;
      height: 8.2rem;
      border: none;
      background: #fff;
}
.my-dialog-hover .top-box .triangle-left {
      position: absolute;
      left: -3px;
      top: -9rem;
      width: 0;
      height: 0;
      border: solid #d84247;
      border-top-width: 4.1rem;
      border-left-width: 1.95rem;
      border-right-width: 1.95rem;
      border-bottom-width: 4.1rem;
      border-bottom-color: transparent;
      border-right-color: transparent;
}
.my-dialog-hover .dialog-panel {
    position: relative;
    display: block;
    width: 66%;
    left: 17%;
    background: #fff;
    border: none;
    margin: 0;
}
.my-dialog-hover .dialog-panel:before {
      content: "";
      position: absolute;
      bottom: -8.1rem;
      right: 0;
      border-width: 8.2em 3.9rem 0 0;
      border-style: solid;
      border-color: #fff transparent;
}
.my-dialog-hover .dialog-panel:after {
      content: "";
      position: absolute;
      bottom: -8.1rem;
      left: 0;
      right: 3.9rem;
      background: #fff;
      height: 8.2rem;
}
.my-dialog-hover .dialog-panel .dialog-content {
      position: relative;
      font-size: 1.4rem;
      color: #999;
      font-style: italic;
      z-index: 999;
      text-align: left;
      padding: 0 1.6rem;
}
.my-dialog-hover .dialog-panel .dialog-content.longText {
        margin-top: -4.4rem;
        margin-bottom: -4.4rem;
}
.my-dialog-hover .dialog-panel .dialog-content .dialog-title {
        color: #d84247;
        font-style: italic;
        font-size: 2.4rem;
        font-weight: bold;
        position: relative;
        top: -6rem;
        text-align: center;
}
.my-dialog-hover .dialog-panel .dialog-content .dialog-title img {
          display: inline-block;
          height: 2.8rem;
}
.my-dialog-hover .dialog-panel .dialog-content .dialog-text {
        position: absolute;
        top: -2rem;
}
.my-dialog-hover .dialog-panel .triangle-right {
      position: absolute;
      right: -3px;
      bottom: -9rem;
      width: 0;
      height: 0;
      border: solid #d84247;
      border-top-width: 4.1rem;
      border-left-width: 1.95rem;
      border-right-width: 1.95rem;
      border-bottom-width: 4.1rem;
      border-top-color: transparent;
      border-left-color: transparent;
}
.my-dialog-hover .close-btn {
    width: 3.3rem;
    height: 3.3rem;
    margin: 0 auto;
    margin-top: 10rem;
    border-radius: 50%;
    border: 2px solid #d84247;
    text-align: center;
    color: #fff;
    font-size: 40px;
    line-height: 3.5rem;
}

.container {
  width: 100%;
  height: 100%;
  background: #fcfcfc;
}
.container #stage {
    width: 100%;
    height: 100%;
}
.container #stage canvas {
      width: 100%;
      height: 100%;
}
.container .score-panel {
    width: 8.2rem;
    height: 13.3rem;
    padding: 0.5rem;
    background: rgba(255, 255, 255, 0.67);
    position: fixed;
    left: 0;
    top: 0;
    text-align: center;
}
.container .score-panel .score-box {
      border: 5px solid #a11c20;
      border-radius: 5px;
      width: 100%;
      -webkit-box-sizing: border-box;
              box-sizing: border-box;
      height: 8.25rem;
      background: #fff;
}
.container .score-panel .score-box .box-item {
        -webkit-box-sizing: border-box;
                box-sizing: border-box;
        height: 50%;
        line-height: 4.1rem;
        font-size: 1.6rem;
        font-weight: bold;
}
.container .score-panel .score-box .box-item.num {
          font-size: 2rem;
}
.container .score-panel .ranking {
      width: 8.2rem;
      height: 3.3rem;
      text-align: center;
      color: #fff;
      background: #e03b40;
      border-radius: 5px;
      margin-top: 4px;
      font-size: 2.2rem;
      line-height: 3.3rem;
      -webkit-box-shadow: 0px 2px 0px 2px #a11c20;
              box-shadow: 0px 2px 0px 2px #a11c20;
}

.powerline {
  position: fixed;
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  width: 40%;
  height: 0;
  padding-bottom: 40%;
  bottom: 0;
  overflow: hidden;
}
.powerline .my-svg {
    position: absolute;
    z-index: 99;
}
.powerline .my-svg .inner-svg {
      -webkit-transform-origin: center;
              transform-origin: center;
      transform-box: fill-box;
      -webkit-transform: rotate(30deg);
              transform: rotate(30deg);
}
.powerline .outer-cycle {
    position: absolute;
    bottom: 0;
    width: 100%;
    height: 0;
    outline: none;
    padding-bottom: 100%;
    border-radius: 50%;
    background: #d35155;
    -webkit-transform: translateY(40%);
            transform: translateY(40%);
}
.powerline .inner-cycle {
    z-index: 100;
    position: absolute;
    bottom: 0;
    width: 64%;
    left: 18%;
    height: 0;
    outline: none;
    padding-bottom: 64%;
    border-radius: 50%;
    background: #d35155;
    -webkit-box-shadow: 0px 3px 5px 5px rgba(0, 0, 0, 0.37);
            box-shadow: 0px 3px 5px 5px rgba(0, 0, 0, 0.37);
    -webkit-transform: translateY(35%);
            transform: translateY(35%);
    text-align: center;
}
.powerline .inner-cycle .inner-cycle-icon {
      display: inline-block;
      width: 0;
      height: 0;
      border: solid #fff;
      border-top-width: 1rem;
      border-left-width: 1rem;
      border-right-width: 1rem;
      border-bottom-width: 1rem;
      border-left-color: transparent;
      border-right-color: transparent;
      border-top-color: transparent;
}
.powerline .inner-cycle .inner-cycle-line {
      display: inline-block;
      width: 2rem;
      height: 0;
      border-top: 3px solid #fff;
      vertical-align: top;
}
.powerline .inner-cycle .inner-cycle-text {
      position: relative;
      display: inline-block;
      font-size: 1.4rem;
      color: #fff;
      font-weight: bold;
      margin-top: -0.4rem;
}
.powerline .shotBtn {
    position: absolute;
    bottom: 0;
    width: 50%;
    left: 50%;
    height: 0;
    outline: none;
    padding-bottom: 50%;
    border-radius: 50%;
    background: #444;
    color: #fff;
    text-align: center;
    line-height: 45px;
    border: none;
    -webkit-transform: translateY(50%) translateX(-50%);
            transform: translateY(50%) translateX(-50%);
}

.option-btn {
  position: fixed;
  bottom: 0;
  text-align: center;
  width: 30%;
  height: 0;
  padding-bottom: 20%;
  background: #fff;
  text-align: center;
  line-height: 4rem;
}
.option-btn canvas {
    position: absolute;
    bottom: 0;
    left: 0;
}
.option-btn.left {
    left: 0;
    background: url(data:image/png;base64,) no-repeat;
    background-size: 100%;
}
.option-btn.right {
    right: 0;
    background: url(data:image/png;base64,) no-repeat;
    background-size: 100%;
}
.option-btn.right .option-btn-text {
      left: 60%;
}
.option-btn .option-btn-text {
    color: #fff;
    position: absolute;
    display: inline-block;
    height: 2rem;
    line-height: 2rem;
    font-size: 1.4rem;
    font-weight: bold;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    bottom: 0;
    left: 40%;
}

